export const getModules = (partner) => {
  const pattern = partner.includeservices;
  if (!pattern) return [];

  const regex = new RegExp(`(${pattern})`, "i");
  const excludeRegex = partner.excludeservices ? new RegExp(`(${partner.excludeservices})`, "i") : null;

  const partnerModules = [...modules];
  partnerModules.forEach(module => {
    const partnerServices = module.services.filter(service => 
      regex.test(service.name) && (!excludeRegex || !excludeRegex.test(service.name))
    );
    module.services = partnerServices;
  });
  
  return partnerModules.filter(module => module.services.length > 0);
}


